@import 'variables';
@import 'mixin';
//@import 'layout.front';
@import 'colorchip';
@import 'goods';
@import 'posts';

.page-head {
	@include hidden;
}

.local-body {
	padding: 0 0.5rem;
	@include respond-above(x-small) {padding: 0 1rem;}
	@include respond-above(small) {padding: 0 1.25rem;}

	.content-head .content-title {
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		right: 7rem;
		left: 7rem;
		margin: 0;
		padding: 0;
		height: 3rem;
		background-color: $color-base-000;

		.content-name {
			@include font-body(medium);

			display: block;
			margin: 0;
			color: $color-base-090;
			white-space: nowrap;
			overflow: hidden;
			font-weight: 400;
			text-overflow: ellipsis;
		}
	}

	.content-body {
		padding: 0 !important;
		margin: 0 0 2rem 0;

		// no-data
		.info-board.no-data {
			padding: 4rem 0;
		}
	}

	//layout.front.scss  
	.content-panel .button-area .btn.share {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 2.5rem;
		min-width: 2.5rem;
		text-align: center;

		svg {
			margin: 0 auto;
			fill: $color-base-070;
		}
	}
}

.content-navigations {
	.navi-list {
		display: flex;
		flex-wrap: wrap;
		margin: 0;
		padding: 0;

		.navi-item {
			@include font-body(small);

			display: flex;
			align-items: stretch;
			list-style: none;
			width: calc(50% - .125rem);
			font-family: "Noto Sans KR", sans-serif;

			&:nth-child(even) {margin-left: 0.25rem;}
			&:nth-child(n+3) {margin-top: 0.25rem;}

			.navi-name {
				display: flex;
				align-items: center;
				flex-grow: 1;
				padding: .5rem 1rem;
				color: $color-base-070;
				text-decoration: none;
				background-color: $color-base-010;
			}

			&.current .navi-name {
				font-weight: 700;
				color: $color-primary-080;
				background-color: $color-primary-010;
			}
		}
	}

	.more-data {
		display: flex;
		justify-content: center;
		margin-top: 1rem;
	}

	&.active .more-data {
		display: none;

		.btn.symbol-rtl-chevron-under::after {
			transform: rotate(180deg);
		}
	}
}

//   
.temp {
	background-color: rgba(255,0,0,.5);
	height: 3rem;
	line-height: 3rem;
	color: $color-base-000;
	text-align: center;
}

//   07.01
.etc-intro {
	.content-head {
		@include hidden;
	}

	.content-body {
		position: relative;

		.info-board.style-c.type-a {
			margin: 1.5rem 0;
		}

		.section.default {
			position: relative;
			margin: 1.5rem 0;

			+.section.default {
				margin-top: 24px;
				padding-top: 24px;

				&::before {
					content: '';
					position: absolute;
					top: 0;
					left: calc(50% - 50vw);
					width: 100vw;
					height: 1px;
					background: $color-base-020;
				}
			}

			.subsection {
				margin-top: 1.5rem;
			}

			.section-head {
				margin-bottom: 1rem;

				.section-name {
					@include font-body(medium);

					margin: 0;
					font-weight: 700;
					color: $color-base-090;
				}
			}

			&.useful-tool .section-body .navi-list {
				display: flex;
				padding: 0;
				margin: 0;

				.navi-item {
					list-style: none;
					width: 25%;

					.navi-name {
						@include font-body(x-small);

						display: flex;
						flex-direction: column;
						align-items: center;
						text-decoration: none;
						color: $color-base-060;

						&::before {
							content: '';
							display: block;
							width: 2.5rem;
							height: 2.5rem;
							margin-bottom: .5rem;
							background-position: center top;
							background-repeat: no-repeat;
							background-size: 2.5rem 2.5rem;
						}
					}

					&.calculator .navi-name::before		{background-image: url('../images/svg/icon_calculator.svg')}
					&.paint-colorchip .navi-name::before{background-image: url('../images/svg/icon_paint_colorchip.svg')}
					&.wood-guide .navi-name::before		{background-image: url('../images/svg/icon_wood_guide.svg')}
					&.easy-cut .navi-name::before		{background-image: url('../images/svg/icon_easy_cut.svg')}
				}
			}

			&.benefit .section-body {
				.navi-list {
					display: flex;
					flex-wrap: wrap;
					padding: 0;
					margin: 0;

					.navi-item {
						list-style: none;
						position: relative;
						display: flex;
						align-items: center;
						width: 50%;

						&:nth-child(n+3) {margin-top: 1rem}

						&::before {
							content: '';
							display: block;
							margin-right: .5rem;
							width: 1.25rem;
							min-width: 1.25rem;
							height: 1.25rem;
							background-position: left center;
							background-size: 100% auto;
						}

						&.roulette::before	{background-image: svgIcon(roulette, $color-base-070)}
						&.coupon::before	{background-image: svgIcon(coupon, $color-base-070)}
						&.event::before		{background-image: svgIcon(present, $color-base-070)}
						&.benefits::before	{background-image: svgIcon(crown, $color-base-070)}

						.navi-name {
							@include font-body(small);

							color: $color-base-090;

							&:not(:hover) {text-decoration: none}
						}
					}
				}

				.display-board.get-point {margin-top: 1.5rem;}
			}

			&.customer-service {
				.section-head {margin-bottom: 0.5rem}

				.section-body .navi-list {
					display: flex;
					flex-wrap: wrap;
					padding: 0;
					margin: 0;

					.navi-item {
						list-style: none;
						position: relative;
						display: flex;
						align-items: center;
						width: 50%;

						&:nth-child(n+3) {margin-top: 1rem}

						.navi-name {
							@include font-body(small);

							color: $color-base-090;

							&:not(:hover) {text-decoration: none}
						}
					}
				}
			}
		}

		.section.alram {
			position: absolute;
			top: 0;
			right: 0;

			.section-head {
				@include hidden;
			}

			.btn.alarm {
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 50%;
				border: 1px solid $color-base-030;
				width: 3rem;
				height: 3rem;

				svg {fill: $color-base-070;}
			}

			.btn.active svg {fill: $color-accent-070;}
		}

		>.button-area.style-b {
			margin: 1rem 0;
		}
	}
}

//  07.02
.event-roulette {
	.image {display: block;}

	.section.roulette {
		position: relative;
		width: 100vw;
		left: calc(50% - 50vw);
		padding: 2.75rem 1.438rem 2.375rem;
		margin-bottom: 1.125rem;
		background-color: #007950;
		background-image: url('../images/etc/img_roulette_background.png');
		background-size: 100% auto;
		background-repeat: no-repeat;
		box-sizing: border-box;

		.section-head {
			margin-bottom: 2.125rem;

			.section-title {
				text-align: center;

				.section-name {
					margin: 0 0 0.75rem 0;

					.image {margin: 0 auto}
				}
			}

			.section-intro .summary {
				@include font-body(small);

				margin: 0;
				text-align: center;
			}
		}

		.section-body {
			.roulette-content {
				position: relative;

				.roulette-board {
					width: 100%;
					padding-bottom: 17.8%;
					position: relative;
					background-image: url('../images/etc/img_roulette_board.png');
					background-size: 100%;
					background-repeat: no-repeat;

					.roulette-target {
						width: 100%;
						padding: 4%;
						box-sizing: border-box;

						.image {
							transition: all 1s ease;
							transform-origin: 50% 50%;
							width: 100%;
						}
					}

					&::before {
						content: '';
						position: absolute;
						top: -.875rem;
						left: 50%;
						margin-left: -6.66vw;
						width: 13.33vw;
						height: 13.33vw;
						background:url('../images/etc/img_roulette_pin.png');
						background-size: 100%;
						z-index: 2;
					}
				}

				.roulette-button {
					position: absolute;
					top: 32%;
					left: 50%;
					padding: 0;
					margin-left: -10.555vw;
					width: 21.11vw;
					height: 21.11vw;
					background: none;
					border: 0;
					outline: 0;
				}
			}

			.para {
				@include font-body(x-small);

				color: #b2d8cb;
				text-align: center;
				margin: -11.5% 0 1.875rem 0;
			}

			.subsection {
				.subsection-head {@include hidden}

				.subsection-body .data-list {
					display: flex;
					border: 1px solid #009b66;
					padding: 0.875rem 0;
					border-radius: 41px;

					.data-item {
						flex-basis: 50%;
						position: relative;

						+.data-item::before {
							content: '';
							position: absolute;
							top: 0;
							left: 0;
							width: 1px;
							height: 100%;
							border-left: 1px dashed #009b66;
						}

						.data-area {
							text-align: center;

							.head, .body {
								display: block;
								color: #fcef96;
							}

							.head {@include font-body(small)}
							.body {@include font-head(medium)}
						}
					}
				}
			}
		}
	}

	.calendar-area {
		.calendar-month {
			display: flex;
			align-items: baseline;
			justify-content: center;
			margin-bottom: 1rem;

			.value {
				@include font-head(large);

				font-family: 'Lato', sans-serif;
				font-weight: 700;
				color: $color-primary-080;
				margin-right: 0.1875rem;
			}

			.unit {
				@include font-body(medium);

				font-weight: 700;
				color: $color-primary-080;
			}
		}

		table {
			table-layout: fixed;
			width: 100%;
			border: 0 none transparent;
			border-spacing: 0;
			padding: 0;
			border-collapse: collapse;

			caption {
				@include hidden;
			}

			thead th {
				@include font-body(x-small);

				color: $color-primary-080;
				font-weight: 700;
				padding-bottom: .5rem;
				border-bottom: 1px solid $color-primary-080;
			}

			tbody {
				tr {
					&:first-child td {
						padding-top: 1.25rem;
					}

					&:last-child td {
						padding-bottom: 0;
					}
				}

				td {
					padding: 0 0 8px 0;

					.day-number {
						@include font-body(small);
						
						position: relative;
						display: flex;
						align-items: center;
						justify-content: center;
						width: 2.25rem;
						height: 2.25rem;
						color: $color-base-070;
						margin: 0 auto;

						&::before {
							content: '';
							position: absolute;
							top: 50%;
							left: 50%;
							margin: -50% 0 0 -50%;
							width: 100%;
							height: 100%;
							background-color: $color-base-010;
							border-radius: 50%;
							z-index: -1;
						}
					}

					&.checked .day-number::before {
						background-color: inherit;
						background-image: url('../images/etc/img_roulette_stamp.png');
						background-size: 100%;
						z-index: 2;
					}
				}
			}
		}
	}

	.widget.tab-bars {@include hidden}
}

//  07.03
.coupon .button-area {
	margin-top: 1.5rem;
}

//  07.05.T01, 07.05.T02, 07.05.T03, 07.05.T04, 07.05.T05
.use-infomation {
	.content-navigations {
		margin: 1.5rem 0 2rem;
	}

	.section.default {
		+ .section.default {
			margin-top: 2rem;
		}

		.section-head {
			padding-bottom: 1rem;
			margin: 0 0 1rem 0;
			box-shadow: 0 .0625rem 0 0 $color-base-020;

			.section-name {
				@include font-body(medium);

				margin: 0;
				font-weight: 700;
				color: $color-base-090;
			}
		}

		.section-body > .para {
			@include font-body(small);

			margin: 0;
			color: $color-base-070;

			+ .para {
				margin-top: 0.5rem;
			}
		}

		.data-table.style-b td {
			a {
				text-decoration: none;
			}

			.data-list .para.style-a {
				margin-top: 0.25rem;
			}

			.title {
				@include font-body(x-small);

				font-weight: 700;
				color: $color-base-070;
				margin: 0.25rem 0 0 0;
			}
		}
	}

	.delivery-infomation .section.default {
		+ .section.default {
			margin-top: 2rem;
		}

		.section-head {
			padding-bottom: 0;
			box-shadow: none;
		}

		.section-body .data-list.style-a + .para.style-a {
			margin-top: 0.5rem !important;
		}
	}

	.coupon {
		position: relative;
		width: 14rem;
		height: 7.625rem;
		margin: 1.5rem auto 0;
		background-image: url(data:image/svg+xml;base64,);

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 4.813rem;
			left: 9.625rem;
			width: 5.375rem;
			height: 3.25rem;
			background-image: url(../images/svg/img-point-character.svg);
			background-repeat: no-repeat;
			background-position: right bottom;
		}

		.coupon-name {
			@include font-body(small);

			display: block;
			position: absolute;
			top: 1.25rem;
			left: 1.25rem;
			color: $color-base-000;
			font-weight: 700;
		}

		.coupon-data {
			display: flex;
			align-items: baseline;
			position: absolute;
			bottom: 1.25rem;
			left: 1.25rem;
			color: $color-base-000;

			.value {
				@include font-head(large);

				font-weight: 700;
				font-family: Lato, sans-serif;
			}
			.unit {
				@include font-body(medium);

				font-weight: 700;
			}
		}
	}
}

//   07.09
.service-guide {
	.content-navigations {
		margin: 1.5rem 0 0.5rem;
	}

	.posts-display.style-a .posts-list .posts-item {
		&:not(.active) .reply-area {
			display: none;
		}

		.posts-body {
			display: flex;

			.posts-summary {
				order: 1;
				flex-grow: 1;
				margin: 0;
				color: $color-base-070;
			}
		}
	}
}

// 1:1  07.07 /   07.10.02
.inquire-write, .request-write {
	.submit-form.theme-a.style-a {
		fieldset {
			& + .option-select {
				padding-top: 1rem;
			}
		}

		.button-area {
			margin-top: 1.5rem;
		}
	}
}

//  07.04,  07.06,   07.08,  07.10
