	.submit-form {
		margin: 1.5rem 0 0.5rem;
	}
}

//  07.11
.notification-list {
	.content-body {
		position: relative;
		margin-bottom: 0;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: calc(50% - 50vw);
			width: 100vw;
			height: 100%;
			background: $color-base-020;
			z-index: -1;
		}
	}

	.tab-display.style-b {
		position: relative;
		width: 100vw;
		left: calc(50% - 50vw);
	}

	.posts-display.style-f {
		position: relative;
		padding: 1.5rem 0 2rem;
	}

