//    07.12
.suggestion-write .suggestion-notice {
	padding: 1.5rem 0 1rem;
	margin-bottom: 1rem;
	box-shadow: 0 0.0625rem 0 0 $color-base-020;

	.para {
		margin: 0;

		+ .para {margin-top: 0.25rem;}
	}
}
//   07.13
.app-setting {
	padding-top: 0.5rem;

	.data-display {
		position: relative;
		left: calc(50% - 50vw);
		width: 100vw;
	}
}

//   01.02.T01, 01.02.T02
.recently-view {
	.content-head {
		@include hidden;
	}

	.tab-display.style-b {
		position: relative;
		left: calc(50% - 50vw);
		width: 100vw;
	}

	.section {
		.section-head {
			@include hidden;
		}

		.info-board.no-data {
			padding: 5.5rem 0 4.75rem;
		}

		.button-area.style-a {
			margin-bottom: 0;
		}

		.posts-display.style-c.type-c .posts-item:first-child {
			padding-top: 0;
		}
	}
}

.unified-search .content-body {
	.info-board.no-data {
		margin: 4rem 0;

		mark.em.style-a {
			background: transparent;
		}
	}

	.section {
		&.default {
			position: relative;
			margin-top: 2rem;

			.section-head {
				margin-bottom: 0.5rem;

				.section-name {
					@include font-body(x-small);

					margin: 0;
					color: $color-base-050;
				}
			}

			.info-board.no-data {
				margin: 1rem 0;
			}

			.recent-list {
				display: flex;
				position: relative;
				left: calc(50% - 50vw);
				width: 100vw;
				box-sizing: border-box;
				overflow-x: auto;
				-webkit-overflow-scrolling: touch;
				-ms-overflow-style: none;
				scrollbar-width: none;

				&::-webkit-scrollbar {display: none;}

				.keyword-area.style-a {
					flex-shrink: 0;
					flex-wrap: nowrap;
					padding: 0 .5rem;
					box-sizing: border-box;

					@include respond-above(x-small) {padding: 0 1rem;}
					@include respond-above(small) {padding: 0 1.25rem;}

					.keyword {
						flex-shrink: 0;

						&:last-child {
							margin-right: 0;
						}
					}
				}
			}

			.popular-search {
				margin: 0;
				padding: 0;
				list-style: none;

				.search-item {
					display: flex;
					align-items: centaer;

					+ .search-item {
						margin-top: 1rem;
					}

					.rank {
						@include font-body(small);

						width: 1rem;
						margin-right: 1rem;
						color: $color-primary-080;
						text-align: center;
					}

					.name {
						@include font-body(small);

						color: $color-base-070;
					}
				}
			}

			.section-side {
				position: absolute;
				top: 0;
				right: 0;

				.btn.style-text {
					vertical-align: top;
				}
			}
		}

		&.associated-search {
			position: relative;
			left: calc(50% - 50vw);
			width: 100vw;
			display: flex;
			align-items: center;
			padding: .5rem 0 1rem .5rem;
			box-sizing: border-box;

			@include respond-above(x-small) {padding: 0.5rem 0 1rem 1rem;}
			@include respond-above(small) {padding: 0.5rem 0 1rem 1.25rem;}

			&::after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: .5rem;
				background: $color-base-020;

			}

			.section-head {
				flex-shrink: 0;
				position: relative;
				margin-right: 0.5rem;

				.section-name {
					@include font-body(x-small);

					display: flex;
					font-weight: 700;
				}
			}

			.section-body {
				flex-grow: 1;
				display: flex;
				position: relative;
				box-sizing: border-box;
				overflow-x: auto;
				-webkit-overflow-scrolling: touch;
				-ms-overflow-style: none;
				scrollbar-width: none;
				padding-left: 0.5rem;

				&::-webkit-scrollbar {
					display: none;
				}

				.keyword-area {
					flex-shrink: 0;
					flex-wrap: nowrap;
					padding: 0 .5rem 0 0;
					box-sizing: border-box;

					@include respond-above(x-small) {padding: 0 1rem 0 0;}
					@include respond-above(small) {padding: 0 1.25rem 0 0;}

					.keyword {
						flex-shrink: 0;

						&:last-child {
							margin-right: 0;
						}
					}
				}
			}
		}

		&.search-resulte {
			.section-head {
				@include hidden;
			}

			.tab-display.style-b {
				position: relative;
				left: calc(50% - 50vw);
				width: 100vw;
			}

			.subsection {
				.subsection-head {
					@include hidden;
				}

				.btn.style-text[class*=symbol-]:before {
					width: 1rem;
					vertical-align: center;
				}

				.button-area.style-a {
					margin-bottom: 0;
				}

				.posts-display.style-c.type-c .posts-head {
					min-height: 4.25rem;
				}

				.goods-display.style-a.type-a .goods-name strong {
					display: block;
					text-align: center;
				}

				.info-board.style-g.type-a {
					margin-bottom: 1rem;
				}
			}
		}
	}

	.recommend-keyword {
		.keyword-list {
			padding: 0;
			margin: 0;
			list-style: none;
			display: flex;
			flex-wrap: wrap;

			.keyword-item {
				flex-basis: calc(49.95% - .375rem);
				flex-shrink: 1;

				&:nth-child(even) {margin-left: 0.75rem;}
				&:nth-child(n+3) {margin-top: 1rem;}

				.keyword-figure {
					margin-bottom: .5rem;
					border-radius: .25rem;
					overflow: hidden;

					.keyword-thumbnail {
						display: block;

						.image {display: block;}
					}
				}

				.keyword-inform .keyword-title {
					text-align: center;
					vertical-align: top;

					.keyword-name {
						@include font-body(small);

						text-decoration: none;
						color: $color-base-070;
						font-weight: 700;
						vertical-align: top;
					}
				}
			}
		}

		.button-area {
			margin-top: 2rem;
		}
	}
}